import React, { useState } from 'react'
import PropTypes from "prop-types"
import styled from 'styled-components'

// takes a string as prop
// appends one letter at a time to displayed string 
// blinking cursor infite - pink 

// start setinterval, cursor blinks 3 times
// stop setinterval, cursor stops blinking and stays visible 
// text begins appending
// text finishes appending
// cursor blinks 3 times
// cursor disappears?

const Typewriter = (text) => {

  const Container = styled.div`
    font-size: 2em;
    align-items: center;
    width: 100%;
  `
  const Cursor = styled.span`
    color: 'lavendarblush';
    opacity = ${props => props.visibleCursor ? 100 : 0}
  `

  const [printedText, setText] = useState('')
  const updateText = () => {
    setInterval()
  }
  const [visibleCursor, setCursor] = useState(true) 
  const updateCursor = () => {
    setCursor(!visibleCursor)
  }

  const Show = () => {
    // run set interval
    setInterval(updateCursor,500)
  }

  Show()

  return (
    <Container>
      <span>{printedText}</span>
      <Cursor visibleCursor={visibleCursor}>|</Cursor>
    </Container>
  )
}

Typewriter.PropTypes = {
  text: PropTypes.string,
}

Typewriter.defaultProps = {
  text: 'Lorem ipsum',
}

export default Typewriter